/* General Layout */
.form-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: #f7f7f7;
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

/* Header Styling */
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

h1 {
  font-size: 2em;
  color: #333;
  text-align: center;
}

.logo {
  width: 50px;
  height: 50px;
  margin-top: 10px;
}

/* Form Container Styling */
.form-container {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 900px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 15px;
}

label {
  display: block;
  font-size: 1em;
  color: #333;
  margin-bottom: 5px;
  text-align: left;
}

/* Increased spacing between form sections */
.form-section {
  margin-bottom: 30px; /* Increased space between questions */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center the inputs */
  text-align: left;
}

/* Input Fields Styling */
input[type="text"],
textarea,
input[type="radio"] {
  display: block;
  width: 100%;
  padding: 12px; /* Increased padding for inputs */
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 10px; /* Added margin to ensure space from question text */
  margin-bottom: 20px; /* Add space below the input fields */
  box-sizing: border-box;
  max-width: 600px; /* Limit the width of the input fields */
}

textarea {
  resize: vertical;
  height: 100px;
}

input[type="radio"] {
  width: auto;
  margin-right: 10px;
}

/* Align radio buttons and labels horizontally */
.form-section div {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.form-section div input[type="radio"] {
  margin-right: 5px;
}

.form-section div label {
  margin-right: 15px;
}

/* Form Button Styling */
.form-button {
  padding: 10px 20px;
  background-color: #74b9ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s ease;
}

.form-button:hover {
  background-color: #1b578c;
}

/* Submit Button Styling */
button[type="submit"] {
  width: 100%;
  padding: 15px;
  background-color: #74b9ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.2em;
  margin-top: 20px;
}

button[type="submit"]:hover {
  background-color: #1b578c;
}

/* Back Button Styling */
.back-button {
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: #333;
  margin-bottom: 20px;
}

.back-button:hover {
  color: #0984e3;
}

/* Spacing between lines and after input fields */
.form-section + .form-section {
  margin-top: 10px; /* Add space between questions */
}

input,
textarea {
  margin-bottom: 20px; /* Space between input areas and the words after them */
}

/* Responsiveness */
@media (max-width: 768px) {
  .form-container {
    width: 90%;
    padding: 20px;
  }

  h1 {
    font-size: 1.5em;
  }

  h2 {
    font-size: 1.2em;
  }
}

